import { Menu, Input } from "antd";
import React, { useState, useEffect } from "react";
import PubSub from "pubsub-js";
import { useNavigate, useLocation } from "react-router-dom";
import { FolderOpenOutlined } from "@ant-design/icons";
import style from "./index.module.scss";
import { NavBarList } from "../../api";
const { Search } = Input;
const onSearch = (value) => console.log(value); // 

const HeaderTop = () => {
  let [items, setItems] = useState([]); // 
  const [current, setCurrent] = useState(null); // 
  const navigate = useNavigate(); // 
  const location = useLocation(); // 
  useEffect(() => {
    NavBarList({ d: "12" }).then((res) => {
      if (res.status === 200) {
        let list = res.data.list.map((v) => {
          return {
            key: v.key,
            label: v.title,
            icon: <FolderOpenOutlined />,
            children: v.children?.map((val) => {
              // url 
              if (val.path === location.pathname) {
                setCurrent(val.key);
              }
              return {
                key: val.key,
                label: val.title,
                path: val.path,
              };
            }),
          };
        });
        setItems(list);
      }
    });
    return () => {};
  }, []);
  // 
  const onClick = (e) => {
    items.find((val) => {
      val.children?.find((v) => {
        if (v.key === e.key) {
          // 
          navigate(v.path, {
            replace: false,
          });
          // 
          PubSub.publish("changePath", e.key);
          setCurrent(e.key);
        }
      });
    });
  };
  return (
    <div className={style.header_top}>
      {" "}
      <div className={style.nav}>
        <div className={style.user}>
          <img
            className={style.logo}
            src={
              "https://thirdwx.qlogo.cn/mmopen/vi_32/IQ5Ux3dw2YiagIt4j9N7HmE0EZmRiaYpibRl2aHicexicqQyC63CxO20qUTCymhlOkLaCJp9O59hELVuePaL1zbicRlw/132"
            }
            alt=""
          />{" "}
          <span>water</span>{" "}
        </div>
        <Menu onClick={onClick} selectedKeys={[current]} mode="horizontal" items={items} />
        <Search size="large" placeholder="input search text" allowClear className={style.search} onSearch={onSearch} />
      </div>
    </div>
  );
};
export default HeaderTop;
