import Index from "../views/index";
import JavaScript from "../views/javaScript";
import Footer from "../layout/Footer";
import React, { lazy } from "react";
import { Navigate } from "react-router-dom";
let Welcome = lazy(() => import("../views/welcome"));
export const routes = [
  {
    path: "/index",
    element: <Index />,
    children: [
      {
        path: "welcome",
        element: <Welcome />,
      },
      {
        path: "contents",
        element: <Navigate to="/index/contents/javascript" />,
      },
      {
        path: "contents",
        element: <Footer />,
        children: [
          {
            path: "javascript",
            element: <JavaScript></JavaScript>,
          },
        ],
      },
    ],
  },

  {
    path: "/",
    element: <Navigate to="/index/welcome" />,
  },
  {
    path: "*",
    element: <Navigate to="/index/welcome" />,
  },
];
